:root{
  --bkg-color:rgb(48, 55, 68);
  --dark-color:rgb(29,32,37); 
  --mid-color:rgb(86, 93, 111);
  --accent-color:rgb(98, 113, 184);
  --light-color:rgb(251, 251, 255);
  background:var(--mid-color);

  --blue-cli:rgb(96,175,237);
  --red-cli:rgb(223,107,115);
  --yellow-cli:rgb(227,190,126);
}

.App {
  box-shadow: 0px 0px 20px 0px rgb(0 0 0 / 20%);
  background:var(--bkg-color);
  max-width:1050px;
  width:calc(90vw - min(600px, 16vw));
  padding-top:3vh;
  margin:auto;
  min-height:100vh;
  padding-left:min(300px, 8vw);
  padding-right:min(300px, 8vw);
}

h1 {
  font-size:min(57pt, 10vw);
  color:var(--light-color);
  font-family:"League Spartan", sans-serif;
  margin-bottom:0em;
}

.type-subheader {
    color:var(--light-color);
    font-family: 'Courier New', Courier, monospace;
    font-size:min(22pt, 3.85vw);
    font-weight:100;
}

.App-menu-container{
  width:80%;
  display: flex; 
  justify-content: space-between; 
  flex-wrap: wrap; 
  color:var(--light-color);
  font-family: 'Courier New', Courier, monospace;
  font-size: min(14pt, 3.0vw);
  margin: 1em auto 1em auto;
}

.App-menu-option{
  width:100vw;
  max-width: 200px;
  text-align:center;
  cursor:pointer;
  transition: transform 100ms;
  height:1.5em;
  padding:1em;
  margin:auto;
}

.App-menu-option:hover{
  opacity:.75;
  transform: scale(1.1);
}

.App-endmatter{
  text-align:center;
  color:var(--light-color);
  opacity:.75;
}

.random-letter{
  opacity:.5;
}

.custom-console{
  width:100%;
  min-height:10em;
  max-width: 840px;
  margin: auto;
  box-shadow: rgba(0, 0, 0, 0.2) 0px 0px 20px 0px;
  font-size:min(12pt, 3vw);
}

.custom-console-header{
  width:calc(100% - 2em);
  margin:auto;
  border-radius:3px 3px 0px 0px;
  background:rgb(35, 35, 42);
  color:var(--light-color);
  padding: .5em 1em .5em 1em;
  font-family:'Courier New', Courier, monospace;
  font-size:min(12pt, 3vw);
}

.custom-console-body{
  width:calc(100% - 2em);
  margin:auto; 
  border-radius:0px 0px 3px 3px; 
  background:rgb(20, 20, 24); 
  color:var(--light-color); 
  padding: .5em 1em 3em 1em;
  font-family:'Courier New', Courier, monospace;
  min-height:500px;
  font-size:min(12pt, 3vw);
}

.custom-console-button-blue{
  color:var(--blue-cli);
   cursor:pointer;
   text-decoration: none;
}

.custom-console-button-red{
  color:var(--red-cli);
   cursor:pointer;
   text-decoration: none;
}

.custom-console-button-yellow{
  color:var(--yellow-cli);
   cursor:pointer;
   text-decoration: none;
}

.custom-console-button-gray{
  color:lightgray;
  opacity:.75;
  cursor:pointer;
}

.custom-console-blue{
  color:var(--blue-cli);
}

.custom-console-red{
  color:var(--red-cli);
}

.custom-console-yellow{
  color:var(--yellow-cli);
}

.home-button{
  color:var(--light-color);
  font-size:min(12pt,3vw);
  cursor:pointer;
  text-decoration: none;
  font-family:'Courier New', Courier, monospace;
}


.page-info-container{
  width:100%;
  min-height:10em;
  max-width: 840px;
  margin: auto;
  margin-top:1em;
  color:var(--light-color);
}

.page-info-container > h2{
  font-size:min(22pt, 3.85vw);
  color:var(--light-color);
  opacity:.75;
  font-family:"League Spartan", sans-serif;
  margin-bottom:.25em;
}
.page-info-container > div{
  font-size:min(12pt,3vw);
  color:var(--light-color);
  margin-bottom:0em;
  padding-left:1em;
  opacity:.8;
  line-height:1.2;
}

.page-info-block{
  line-height:1.8;
}

.page-code-container-small-text{
    width:calc(100% - 4em);
  max-width: calc(840px - 4em);
  font-size:min(9pt, 2.5vw);
  margin: auto;
  margin-top:.5em;
  margin-bottom:.5em;
  background:var(--mid-color);
  background:rgba(20, 20, 24, .5); 
  color:var(--light-color);
  padding:1em;
  border-radius:.5em;
  font-family:'Courier New', Courier, monospace;
}
.page-code-container{
  width:calc(100% - 4em);
  max-width: calc(840px - 4em);
  font-size:min(12pt, 3vw);
  margin: auto;
  margin-top:.5em;
  margin-bottom:.5em;
  background:var(--mid-color);
  background:rgba(20, 20, 24, .5); 
  color:var(--light-color);
  padding:1em;
  border-radius:.5em;
  font-family:'Courier New', Courier, monospace;

}

.page-eq-center-block{
  line-height:1.8;
  text-align:center;
}

.page-img-block{
  width:100%;
  max-width:500px;
  height:auto;
  margin:auto;
}

.page-img{
  border-radius:10px;
}

.mc-option-container{
  width:calc(.9*90vw - .9*min(600px, 16vw));
  font-size:min(12pt,2.7vw);
  max-width:550px;
  max-height:550px;
  margin:auto;
  box-shadow: rgba(0, 0, 0, 0.2) 0px 0px 20px 0px;
  background:rgba(20, 20, 24, .5); 
  padding-left:0px !important;
}
.mc-option-parent{
  width:100%;
  height:auto;
  display: flex; 
  justify-content: space-between; 
  flex-wrap: nowrap; 
}

.mc-option-container-left{
  width:30%;
  padding:1em;
  font-family:"League Spartan", sans-serif;
}
.mc-option-container-right{
  width:calc(70% - 4em);
  padding:1em;
  color:var(--light-color);
}
.mc-path-display{
  background:var(--light-color);
  border-radius:3px;
}

.mc-option-parent-menu{
  width:100%;
  height:calc(100% - 9em);
  display: flex; 
  justify-content: space-between; 
  flex-wrap: nowrap; 
}

.mc-option-container-menu-left{
  width:30%;
  height:calc(100% - 2em);
  padding:1em;
  font-family:"League Spartan", sans-serif;
}
.mc-option-menu-label{
  line-height: 1;
  text-align:right;
}
.mc-option-container-menu-right{
  width:calc(70%);
  height:calc(100% - 2em);
}

.mc-option-pricer-number-input{
  width:calc(80% - 1em);
}

.mc-option-pricer-number-input-mobile{
  width:calc(80% - 1em);
}

.mc-option-pricer-result{
  width: 50%;
  padding:.5em;
  margin:auto;
  margin-top:1em;
  background:var(--mid-color);
  color:var(--light-color);
  border-radius:3px;
  font-family:"League Spartan", sans-serif;
}

.mc-option-menu-table{
  width:calc(100%);
  margin:auto;
  margin-top:1em;
}




.mc-option-parent-mobile{
  width:100%;
  height:auto;
}

.mc-option-container-mobile{
  width:calc(.9*90vw - .9*min(600px, 16vw));
  height:auto;
  font-size:min(12pt,2.7vw);
  max-width:550px;
  margin:auto;
  box-shadow: rgba(0, 0, 0, 0.2) 0px 0px 20px 0px;
  background:rgba(20, 20, 24, .5); 
  padding-left:0px !important;
}

.mc-option-container-top-mobile{
  width:100%;
  height:auto;
}


.mc-option-container-bottom-mobile{
  width:calc(100% - 2em);
  margin-top:1em;
  height:auto;
  padding:1em;
  color:var(--light-color);
  border-top:1px dashed var(--light-color);
}

.mc-path-display-mobile{
  background:var(--light-color);
  border-radius:3px;
  text-align:center;
}

.mc-option-parent-menu-mobile{
  width:100%;
  height:auto;
  display: flex; 
  justify-content: space-between; 
  flex-wrap: nowrap; 
}

.mc-option-parent-menu-left-mobile{
  width:25%;
  text-align:right;
}

.mc-option-parent-menu-right-mobile{
  width:75%;
  text-align:left;
}